@import url(https://fonts.googleapis.com/css2?family=Edu+QLD+Beginner:wght@700&family=Edu+SA+Beginner:wght@700&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: "Edu SA Beginner", cursive;
}
body {
  background-color: rgb(247, 244, 244);
}

.sub-title {
  letter-spacing: 0.2rem;
  border-bottom: 3px solid black;
}

.text-muted {
  line-height: 2em;
}

/* Skill bar */
.progress {
  background-color: rgb(230, 230, 230);
  height: 2em;
  border-radius: 1em;
}

.progress-done {
  opacity: 0;
  width: 0;
  transition: 3s ease;
  height: 2em;
  text-align: end;
  color: #eee;
  border-radius: 1em;
}

/* Course */

a:hover {
  color: olive !important;
}
.tab-content > .tab-pane:not(.active) {
  display: block;
  height: 0;
  overflow-y: hidden;
}

._navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: auto;
  background-color: white;
  z-index: 5;
}

._navbar ul {
  margin: 0 !important;
  padding: 0 !important;
}

.nav-menu {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}
.nav-item {
  border-right: dotted 1px rgba(111, 111, 111, 0.2);
  padding: 20px 0px;
  margin: auto 0px;
  list-style-type: none;
}

.nav-links {
  padding: 28px 20px;
  color: grey;
  text-decoration: none;
  font-size: 16px;
}

.logo {
  font-weight: 900;
  color: black;
  text-decoration: none;
  font-size: 26px;
  margin: auto 0px;
  text-align: center;
}

.nav-links:hover {
  color: olive;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

.active {
  color: olive;
}

@media screen and (max-width: 950px) {
  ._navbar {
    z-index: 2;
  }
  .nav-menu {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
    height: 80vh;
    position: absolute;
    top: 30px;
    right: -100%;
    opacity: 1;
    transition: all 0.5s ease-out;
  }
  .menu-icon {
    display: block;
    position: fixed;
    right: 0;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
    font-size: 1.5rem;
    cursor: pointer;
  }
  .nav-menu.open {
    background: white;
    right: -50%;
    opacity: 0.95;
    transition: all 0.5s ease;
    z-index: 0;
  }
  .nav-links {
    border-bottom: solid 1px grey;
    padding-bottom: 3px;
  }
  .logo {
    border-bottom: none;
    padding-bottom: none;
  }
}

._dropdown-menu {
  width: 200px;
  position: absolute;
  top: 80px;
  list-style: none;
  text-align: start;
  background: grey;
}

._dropdown-menu li {
  background: grey;
  cursor: pointer;
  opacity: 0.8;
}

._dropdown-menu li:hover {
  background: rgb(171, 171, 171);
}

._dropdown-menu.clicked {
  display: none;
}

.dropdown-link {
  display: block;
  height: 100%;
  width: 100%;
  text-decoration: none;
  color: #fff !important;
  padding: 20px 20px;
}

.card {
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
  background-color: white;
}

.card-center {
  margin: 12% auto;
  width: 85%;
  padding: 5%;
}
.card-top {
  border-bottom: dotted 1px rgb(148, 148, 148);
}
.card-title {
  letter-spacing: 3px;
}
.card-top-p {
  margin-top: 10px;
  margin-bottom: 20px;
  font-weight: 200;
}

.card-bottom {
  margin-top: 50px;
  margin-bottom: 20px;
}

.card-bottom-p {
  line-height: 3;
}

.card-bottom > .like-p {
  margin: 15px 0px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.card-bottom > .like-p > .far {
  margin-left: 30px;
  font-size: 2em;
}

.link {
  text-decoration: none;
  color: gray;
}
.link:hover {
  color: olive;
}

@media screen and (max-width: 700px) {
  .card-center {
    margin-top: 30%;
  }
}

.Image_image__H2az8 {
  width: 200px;
  height: 200px;
  border-radius: 50%;
}
.Image_all__1QpKg {
  padding: 2em;
  vertical-align: middle;
  border-top: dotted 0.5px rgb(208, 208, 208);
  border-bottom: dotted 0.5px rgb(208, 208, 208);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.Image_email__1I4FH {
  text-decoration: none;
  color: black;
  font-size: 10px;
}
.Image_email__1I4FH:hover {
  color: olive;
}

.Image_name__3joH2 {
  font-size: 40px;
}
.Image_email__1I4FH,
.Image_image__H2az8,
.Image_name__3joH2 {
  margin: 10px;
  padding: 10px;
}

.about-all {
  margin: 80px 60px;
  padding-bottom: 60px;
  border-bottom: dotted 0.5px rgb(208, 208, 208);
}
.about-title {
  padding-bottom: 50px;
  letter-spacing: 3px;
  font-size: medium;
}

.about-p {
  font-family: "Source Sans Pro", sans-serif;
  line-height: 1cm;
  font-weight: bold;
  padding-bottom: 40px;
}

.about-link {
  margin-right: 20px;
  font-size: smaller;
  color: gray;
  padding: 10px;
  border: 1px solid;
  text-decoration: none;
}

.about-link:hover {
  color: olive;
  border-color: olive;
}

.footer {
  color: rgb(153, 153, 153);
  margin: 25px 60px;
}
.icon {
  display: inline;
}

.iconitem {
  margin-right: 10px;
  font-size: 20px;
  color: rgb(153, 153, 153);
  opacity: 0.6;
}
.iconitem:hover {
  color: olive;
}
.copyright {
  margin-top: 20px;
  font-size: 8px;
  opacity: 0.6;
}
.fa-copyright {
  font-size: 6px;
}
.copyright-email {
  color: rgb(153, 153, 153);
  text-decoration: underline;
  margin-left: 5px;
}
.copyright-email:hover {
  color: olive;
}

.projectItem {
  border: 1px solid rgb(171, 171, 171);
  margin: 60px 40px;
  box-shadow: -5px -5px rgb(233, 233, 233);
}

.project-name,
.project-year {
  margin: 10px 15px;
}

.project-year,
.project-link {
  font-family: "Stick", sans-serif;
}

.project-year {
  font-size: 0.6em;
}

.project-down {
  position: relative;
  height: 400px;
  background-size: 100% 100%;
}

.project-description {
  font-family: "Stick", sans-serif;
  padding: 3% 1%;
  color: rgb(84, 84, 66);
  width: 98%;
  background-color: rgba(254, 247, 247, 0.6);
  z-index: 1;
  position: absolute;
  bottom: 0;
  left: 0;
}

